import React from "react";
import {
  ReactiveBase,
  DataSearch,
  ReactiveList,
} from "@appbaseio/reactivesearch";

function Search() {
  return (
    <ReactiveBase
      url="https://oin-us-east-1.searchly.com"
      app="telegram_media"
      transformRequest={(data) => {
        let sample = JSON.stringify({
          id: "default_search",
          params: {
            search_term: document.getElementById("searchbox-downshift-input")
              .value,
          },
        });
        data.url = data.url.replace("msearch", "search/template");
        "".split("\n");
        data.body = sample;

        return data;
      }}
      credentials="paas:2c5899756b580a78ba3d8e883d6cabd2"
      enableAppbase={false}
    >
      <div className="flex w-full">
        <DataSearch
          className="w-full p-10 bg-gray-900"
          componentId="searchbox"
          icon={
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 opacity-30"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          }
          highlight={true}
          dataField={["caption"]}
          placeholder="Search for Movies and Series"
        />
      </div>
      <ReactiveList
        componentId="results"
        className="bg-gray-700"
        size={10}
        pagination={true}
        react={{
          and: ["searchbox", "authorsfilter", "ratingsfilter"],
        }}
        render={({ data }) => (
          <ReactiveList.ResultCardsWrapper className="flex flex-row flex-wrap">
            {data.map((item) => (
              <a
                href={"http://obscure-falls-12877.herokuapp.com/" + item._id}
                className="m-10 w-4/12 flex-wrap  py-6 flex flex-col justify-center hover:animate-bounce"
              >
                <div className="" key={item._id}>
                  <div className="py-3 sm:mx-auto">
                    <div className="bg-white shadow-lg border-gray-100 max-h-80	 border sm:rounded-3xl p-8 flex space-x-8">
                      <div className="h-44 overflow-visible w-1/2">
                        <img
                          className="rounded-3xl shadow-lg"
                          src={
                            "https://www.themoviedb.org/t/p/w600_and_h900_bestv2" +
                              item.poster || item.imageUrl
                          }
                          alt=""
                        />
                      </div>
                      <div className="flex flex-col w-1/2 space-y-4">
                        <div className="flex justify-between items-start">
                          <h2 className="text-xl font-bold capitalize">
                            {item.caption}
                          </h2>
                          <div className="bg-yellow-400 font-bold rounded-xl p-2">
                            {item.rating}
                          </div>
                        </div>
                        <div>
                          <div className="text-sm text-gray-400">
                            {item.type}
                          </div>
                          <div className="text-lg text-gray-800">
                            {item.year}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </a>
            ))}
          </ReactiveList.ResultCardsWrapper>
        )}
      />
    </ReactiveBase>
  );
}

export default Search;
